/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  overflow-x: hidden;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: underline;
  color: green;
}

h1 {
  color: white;
  font-size: 3rem;
  margin-bottom: 0.625rem;
}

h2 {
  color: white;
  font-size: 2.5rem;
  margin-bottom: 0.625rem;
}

p {
  color: white;
  font-weight: 100;
  font-size: 1.125rem;
}
@media (min-width: 40em) {
  p {
    font-size: 1.375rem;
  }
}

ol {
  color: white;
  font-size: 1.3rem;
  cursor: url("../assets/hlava.png"), auto;
  font-family: var(--font);
}

span {
  text-decoration: underline;
  color: #9494d8;
  font-family: var(--font);
}

button {
  cursor: pointer;
  border-radius: 0.2rem;
  font-family: var(--font);
  color: #fff;
  margin: 1.5rem 1rem;
  background-color: gray;
  border-style: none;
  font-size: 1.2rem;
  padding: 0.2rem 1rem;
  filter: drop-shadow(1px 1px 5px #000);
  transition: 0.1s;
  cursor: url("../assets/hlava.png"), auto;
}
button:hover {
  background-color: var(--blue);
}
@media (min-width: 71.875em) {
  button {
    font-size: 1.5rem;
  }
}

.btn2:hover {
  background-color: lightblue;
}

.btn4:hover {
  background-color: var(--green);
}

.button-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 71.875em) {
  .button-container {
    flex-direction: row;
  }
}

.head-text {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  background-image: url("../assets/pozadie.jpg");
  background-size: cover;
}
.head-text p {
  text-align: center;
  font-size: 1.125rem;
  font-weight: 400;
  color: var(--gray);
  max-width: 80%;
  cursor: url("../assets/hlava.png"), auto;
  font-family: var(--font);
  color: white;
}
@media (min-width: 40em) {
  .head-text p {
    font-size: 1.5rem;
  }
}
.head-text h1 {
  text-align: center;
  font-size: 1.875rem;
  font-weight: 800;
  cursor: url("../assets/hlava.png"), auto;
  font-family: var(--font);
  color: white;
}
@media (min-width: 40em) {
  .head-text h1 {
    font-size: 3rem;
  }
}
.head-text .server-type {
  color: white;
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: black;
  background-image: url(../assets/dia.png);
  text-shadow: 1px 1px 2px black, 0 0 1em white, 0 0 0.2em black;
}
footer .credit {
  font-size: 1.125rem;
}

:root {
  --blue: hsl(204, 100%, 49%);
  --green: hsl(98, 100%, 39%);
}

:root {
  --font: "Merriweather", serif;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.mainpage-info {
  padding: 3.125rem 3.125rem;
  background-color: black;
  background-image: url("../assets/trospyabedrok.jpg");
  background-size: cover;
}

.cleni {
  height: 85rem;
  padding: 0.3125rem 0.0625rem;
  background-color: black;
  background-image: url("../assets/trospympsaci.jpg");
  background-size: cover;
}
@media (min-width: 71.875em) {
  .cleni {
    padding: 3.125rem 6.25rem;
  }
}
@media (min-width: 40em) {
  .cleni {
    height: 93.65vh;
  }
}

.whitelist {
  height: 93.65vh;
  display: flex;
  justify-content: center;
  padding: 3.125rem 6.25rem;
  background-color: black;
  background-image: url("../assets/wl.jpg");
  background-size: cover;
}
.whitelist h1 {
  margin-top: 12.5rem;
}
.whitelist .id-bg {
  background-color: white;
  color: black;
  border-radius: 0.3125rem;
  border-color: black;
  border-style: solid;
}